"use client";

import React from "react";

interface MovieCardProps {
  title: string;
  imageUrl: string;
  rating?: number;
}

export default function MovieCard({ title, imageUrl, rating }: MovieCardProps) {
  return (
    <div className="w-40 flex-shrink-0 rounded overflow-hidden cursor-pointer hover:scale-105 transform transition duration-300 ease-in-out">
      <img src={imageUrl} alt={title} className="w-full h-56 object-cover" />
      <div className="bg-black p-2">
        <h3 className="text-white text-sm font-semibold truncate">{title}</h3>
        {rating !== undefined && (
          <p className="text-gray-400 text-xs mt-1">Rating: {rating}/10</p>
        )}
      </div>
    </div>
  );
}
