"use client";

import React, { useState } from "react";
import { useUser } from "@/context/UserContext";

export default function LandingPage() {
  const { login } = useUser();
  const [email, setEmail] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  async function handleGetStarted(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    // For demo, use email as username and a fixed password
    const success = await login(email, "password");
    setLoading(false);
    if (!success) {
      setError("Failed to login. Please try again.");
    }
  }

  return (
    <div
      className="relative h-screen bg-cover bg-center flex flex-col items-center justify-center text-white px-4"
      style={{
        backgroundImage:
          "url('https://assets.nflxext.com/ffe/siteui/vlv3/3a3a3a3a-3a3a-3a3a-3a3a-3a3a3a3a3a3a/3a3a3a3a-3a3a-3a3a-3a3a-3a3a3a3a3a3a/IN-en-20230619-popsignuptwoweeks-perspective_alpha_website_medium.jpg')",
      }}
    >
      <div className="absolute inset-0 bg-black bg-opacity-70"></div>
      <div className="relative max-w-xl text-center space-y-6">
        <h1 className="text-4xl font-bold">Unlimited movies, TV shows and more</h1>
        <p className="text-lg">Watch anywhere. Cancel anytime.</p>
        <p>Ready to watch? Enter your email to create or restart your membership.</p>
        <form onSubmit={handleGetStarted} className="flex space-x-2">
          <input
            type="email"
            placeholder="Email address"
            className="flex-grow px-4 py-3 rounded text-black focus:outline-none"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <button
            type="submit"
            disabled={loading}
            className="bg-red-600 px-6 py-3 rounded font-semibold hover:bg-red-700 transition"
          >
            {loading ? "Loading..." : "Get Started"}
          </button>
        </form>
        {error && <p className="text-red-500 mt-2">{error}</p>}
      </div>
    </div>
  );
}
