"use client";

import React from "react";
import MovieCard from "./MovieCard";

interface Movie {
  id: number;
  title: string;
  imageUrl: string;
  rating?: number;
}

interface RowsProps {
  title: string;
  movies: Movie[];
}

export default function Rows({ title, movies }: RowsProps) {
  return (
    <section className="mb-8">
      <h2 className="text-white text-xl font-semibold mb-4">{title}</h2>
      <div className="flex space-x-4 overflow-x-auto scrollbar-hide px-1">
        {movies.map((movie) => (
          <MovieCard
            key={movie.id}
            title={movie.title}
            imageUrl={movie.imageUrl}
            rating={movie.rating}
          />
        ))}
      </div>
    </section>
  );
}
